.swiper {
  width: 100%;
  height: 300px;
  margin-left: auto;
  margin-right: auto;
}

.swiper-slide {
  background-size: cover;
  background-position: center;
}

.modalSwiper {
  // height: 80%;
  width: 80%;
}

.modalSwiper2 {
  height: 20%;
  box-sizing: border-box;
  padding: 10px 0;
}

.modalSwiper2 .swiper-slide {
  width: 25%;
  height: 100%;
  opacity: 0.4;
}

.modalSwiper2 .swiper-slide-thumb-active {
  opacity: 1;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 80%;
  object-fit: cover;
}
